.selected-user{
  background: $light-blue;
  color: white;
}
.list-box {
  // position: fixed;
  z-index: 10;
  .p-r-8{
    padding-right: 8px;
  }
}
.list-box:hover{
  .p-r-8{
    padding-right: 0px;
  }
}
.list-item {
  border-bottom: 1px solid #dcdcdc;
}
.list-item:hover {
  background: rgba(204, 204, 204, 0.32);
}
.user-list-container{
  max-width: 300px;
  min-width: 100%;
  // width: 160px;
  transition: width 1s;
  -webkit-transition:width 1s;
  -moz-transition: width 1s;
  max-height: 88vh;
  z-index: 10;
  overflow-y: hidden;
  overflow-x: hidden;
  position: relative;
  // padding-right: 8px;
}
.user-list-container:hover{
  // width : 245px;
  overflow-y: scroll;
  // padding-right: 0px;

  box-shadow: 2px 3px 3px 1px rgba(0, 0, 0, 0.47);
  .list {
    width: 100%;
    .arrow {
      display: none;
    }
  }
}

#userList::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

#userList::-webkit-scrollbar
{
	width: 4px;
	background-color: #F5F5F5;
}

#userList::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #2e3e4e;
}
@media (max-width: 767px) {
  .user-list-container{
    max-height: 100vh;
    overflow-y: auto;
  }
}
